.circularProgressSkillsElement {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  height:200px;
  padding-top: 50px;
}
.circularProgressSkillsElementImportant {
  grid-column: span 2;
  grid-row: span 2;
  height:400px;
}


.singleChart {
  width: 50%;
  justify-content: center ;
  /* flex: 1; */
  margin:auto;
}

.circularChart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  /* max-height: 250px; */
}

.circleBg {
  fill: none;
  stroke: #eee;
  stroke-width: 3.8;
}

.circle {
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
  animation: progress 1s ease-out forwards;
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

.circularChart.orange .circle {
  stroke: #ff9f00;
}

.circularChart.green .circle {
  stroke: #4CC790;
}

.circularChart.blue .circle {
  stroke: #3c9ee5;
}

.percentage {
  font-family: sans-serif;
  font-size: 0.5em;
  text-anchor: middle;
  stroke: none;
}

@media (max-width: 500px) {
  .circularProgressSkillsElement{
    height: 250px;
  }
  .circularProgressSkillsElementImportant{
    height: 450px;
  }
}